<template>
  <div class='myteam'>
    <section v-for='(char, index) in myTeam' :key='index' :class='{"none": char===false}'>
      <div class='myteam-char' v-if='char!==false'>
        <CharFace :char='char' :index='index' />

        <section>
          <div>
            <img
              class='tempSkill'
              v-for='(s, key) in tempQueue(index).reverse()'
              @dblclick='removeFromTempQueue(s)'
              :key='key'
              :src='`/img/game/${skill(s.caster, s.skill).skillpic}.jpg`'
            />
            <img
              class='tempSkill'
              v-for='(s, key) in skillQueue(char.id).reverse()'
              @dblclick='removeFromTempQueue(s)'
              :key='key'
              :src='`/img/game/${s.skillpic}.jpg`'
            />
          </div>
          <Skills
            v-if='myData.isTurn'
            :skills='char.skills'
            :charIndex='index'
            @selected-skill='setSkill'
          />
        </section>
      </div>
    </section>
  </div>
</template>

<script>
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watchers from "./script/watchers";

export default {
  data() {
    return {
      selectedSkill: {},
    };
  },
  components,
  computed,
  methods,
  watchers,
  created() {
    this.$eventBus.$on("set-target", (target) => {
      const room = this.$store.getters["game/getBattleRoom"];
      room.send("add-skill-to-queue", {
        ...this.selectedSkill,
        target: target,
      });
    });
  },
};
</script>

<style lang="scss">
@import "./style";
</style>