<template>
  <section class='enemy-char' v-if='char!==false'>
    <img
      class='tempSkill'
      v-for='(s, key) in tempQueue(index).reverse()'
      @dblclick='removeFromTempQueue(s)'
      :key='key'
      :src='`/img/game/${skill(s.caster, s.skill).skillpic}.jpg`'
    />
    <img
      class='tempSkill'
      v-for='(s, key) in skillQueue(char.id).reverse()'
      @dblclick='removeFromTempQueue(s)'
      :key='key'
      :src='`/img/game/${s.skillpic}.jpg`'
    />

    <div class='enemy-char-status'>
      <div class='enemy-icon'>
        <img
          @click='selectTarget()'
          v-if='isTarget.isTarget'
          class='pulsate'
          :src='`/img/game/${char.facepic}.jpg`'
        />
        <img :src='`/img/game/${char.facepic}.jpg`' />
      </div>

      <div class='enemy-hp'>
        <div class='enemy-hp-value'>{{char.hitPoints}}</div>
        <div class='enemy-hp-gauge' :style='`width: ${char.hitPoints}%`'></div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: ["char", "index"],
  methods: {
    selectTarget() {
      // emit received by MyTeam/index
      this.$eventBus.$emit("set-target", this.index);
      this.$store.commit("game/setSkillInFocus", {});
      this.$store.commit("game/resetTargetables");
    },
    removeFromTempQueue(s) {
      const room = this.$store.getters["game/getBattleRoom"];
      room.send("remove-skill-from-queue", s);
    }
  },  
  computed: {
    isTarget() {
      return this.$store.getters["game/getCharacterByIndex"](this.index);
    },
    tempQueue() {
      return this.$store.getters["game/getTempQueueByCharacter"];
    },
    skill() {
      return this.$store.getters["game/getSkillByCaster"];
    },
    skillQueue(){
      return this.$store.getters['game/getSkillQueueById'];
    }
  }
};
</script>

<style lang="scss">
.tempSkill {
  width: 30px;
  border-radius: 50%;
  margin: 1px;
}
</style>