<template>
  <div class='myteam-char-status'>
    <div class='myteam-char-icons'>
      <!--<img class="pulsate" :src='`/img/game/${char.facepic}.jpg`' /> -->
      <img
        @click='selectTarget()'
        v-if='isTarget.isTarget'
        class='pulsate'
        :src='`/img/game/${char.facepic}.jpg`'
      />
      <img :src='`/img/game/${char.facepic}.jpg`' />
    </div>
    <div class='myteam-hp'>
      <div class='myteam-hp-value'>{{char.hitPoints}}</div>
      <div class='myteam-hp-gauge' :style='`width: ${char.hitPoints}%`'></div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["char", "index"],
  computed: {
    isTarget() {
      return this.$store.getters["game/getCharacterByIndex"](this.index);
    },
  },
  methods: {
    selectTarget() {
      // emit received by MyTeam/index
      this.$eventBus.$emit("set-target", this.index);
      this.$store.commit("game/setSkillInFocus", {});
      this.$store.commit("game/resetTargetables");
    },
  },
};
</script>