<template>
  <div class="ingame">

    <section class='ingame-header'>
      <ul class='ingame-header-energypool' v-if="GetMyData.isTurn">
        <li
          v-for='(amount, key) in GetMyData.energyPool.slice(0,4)'
          :key='key'
          :class='`${energies[key]} ${energies[key]}-glow`'
        >{{amount}}</li>
      </ul>
      <button v-if="GetMyData.isTurn" @click='endTurn()'>end turn</button>
    </section>

    <section class='ingame-field'>
      <MyTeam @selected-a-skill='viewerUpdate'></MyTeam>
      <EnemyTeam></EnemyTeam>
    </section>

    <section class='ingame-footer'>
      <Viewer></Viewer>
    </section>
    <!-- MODALS -->
    <EnergyCart @endturn='endTurn' v-if="endturn && GetMyData.isTurn" :queue="GetMyData.payupCart" @close='endturn=false'/>
  </div>
</template>

<script>
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watch from "./script/watchers";

export default {
  data() {
    return {
      energies: ["speed", "strength", "elemental", "wisdom"],
      endturn: false
    };
  },
  components,
  computed,
  methods,
  watch,
  created() {
    const room = this.$store.getters["game/getBattleRoom"];
    if (room !== null) {
      room.onMessage("start-new-turn", p => {
        this.$store.commit("game/setGameState", p);
        if (this.GetMyData.isTurn)
          this.$store.commit("midori/setUpdate", {
            value: true,
            index: 0
          });
        else
          this.$store.commit("midori/setUpdate", {
            value: true,
            index: 1
          });
      });

      room.onMessage('update-temp-queue', q => {
        this.$store.commit("game/setTempQueue", q)
        this.$store.commit("game/setPlayerEnergyPool", {
          playerIndex: q.playerIndex,
          energyPool: q.energyPool
        })
        this.$store.commit("game/setPayupCart", {
          p: q.playerIndex,
          pay: q.payupCart
        })
      })
    }
  },
  mounted() {
    if (this.GetMyData.isTurn)
      this.$store.commit("midori/setUpdate", {
        value: true,
        index: 0
      });
    else
      this.$store.commit("midori/setUpdate", {
        value: true,
        index: 1
      });
  },
  beforeDestroy(){
    this.$store.commit("game/setRoom", null)
  }
};
</script>

<style lang="scss">
@import "./style";
</style>